<template>
    <div class="container mt-5">
      <h2 class="mb-4">Distribución por Género</h2>
  
      <div class="text-center">
        <canvas id="genderChart" style="max-width: 400px; width: 100%; height: 300px;"></canvas>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import Chart from 'chart.js';
  import { BASE_URL } from '../store/config';
  
  export default {
    data() {
      return {
        niños: [],
        errorLoadingNiños: null,
      };
    },
    computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
},
    methods: {
      loadNiños() {
        axios
          .get(`${BASE_URL}/api/nino`, {
            headers: {
              'x-access-token': this.$store.state.auth.user.accessToken,
            },
          })
          .then(response => {
            this.niños = response.data;
            this.actualizarGrafico();
          })
          .catch(error => {
            this.errorLoadingNiños = 'Error al cargar niños. Por favor, inténtalo de nuevo.';
            console.error('Error al cargar niños:', error);
          });
      },
      actualizarGrafico() {
        // Contar niños por género
        const countMale = this.niños.filter(niño => niño.sexo === 'masculino').length;
        const countFemale = this.niños.filter(niño => niño.sexo === 'femenino').length;
  
        // Configuración del gráfico
        const ctx = document.getElementById('genderChart').getContext('2d');
        const myChart = new Chart(ctx, {
          type: 'doughnut', // Gráfico circular (doughnut)
          data: {
            labels: ['Masculino', 'Femenino'],
            datasets: [{
              data: [countMale, countFemale],
              backgroundColor: ['#36A2EB', '#FF6384'], // Puedes cambiar los colores según tus preferencias
            }],
          },
          options: {
            responsive: true,
            maintainAspectRatio: false,
            legend: {
              display: true,
              position: 'bottom',
            },
          },
        });
      },
    },
    mounted() {
      this.loadNiños();
    },
  };
  </script>
  
  <style scoped>
  /* Estilos adicionales si es necesario */
  </style>
  