<template>
  <div>
    <h2>Formulario de Información  Niño/Niña</h2>

    <div class="form-group">
      <div v-if="message" class="alert alert-danger" role="alert">
        <div v-for="(error, index) in message" :key="index">
          <span v-html="error.msg"></span>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6">
        <button class="btn btn-info btn-sm ml-auto">
          <router-link to="/registros" class="nav-link">
            <span class="text-white">Volver</span>
          </router-link>
        </button>
      </div>
    </div>

    <form @submit.prevent="submitForm" class="needs-validation" novalidate>
      <!-- Fila 1 -->
      <div class="row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="hcu">HCU:</label>
            <input type="text" id="hcu" v-model="formData.hcu" class="form-control" required />
            <div class="invalid-feedback">Este campo es obligatorio.</div>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="cedula">Cédula niño/niña:</label>
            <input type="text" id="cedula" v-model="formData.cedula" class="form-control" required />
            <div class="invalid-feedback">Este campo es obligatorio.</div>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="cedulamadre">Cédula madre:</label>
            <input type="text" id="cedulamadre" v-model="formData.cedulamadre" class="form-control" required />
            <div class="invalid-feedback">Este campo es obligatorio.</div>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="nombres">Nombres:</label>
            <input type="text" id="nombres" v-model="formData.nombres" style="text-transform:uppercase"  class="form-control" required />
            <div class="invalid-feedback">Este campo es obligatorio.</div>
          </div>
        </div>
      </div>

      <!-- Fila 2 -->
      <div class="row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="apellidos">Apellidos:</label>
            <input type="text" id="apellidos" v-model="formData.apellidos" style="text-transform:uppercase" class="form-control" required />
            <div class="invalid-feedback">Este campo es obligatorio.</div>
          </div>
        </div>

        <div class="col-md-4">
          <div class="form-group">
            <label for="discapacidad">Discapacidad:</label>
            <select id="discapacidad" v-model="formData.discapacidad" class="form-control">
              <option value="Si">Sí</option>
              <option value="No">No</option>
            </select>
          </div>
        </div>

        <div class="col-md-4" v-if="formData.discapacidad === 'Si'">
        <div class="form-group">
          <label for="listadoDiscapacidades">Listado de Discapacidades:</label>
          <select id="listadoDiscapacidades" v-model="formData.detalleDiscapacidad" class="form-control">
            <option v-for="discapacidad in discapacidades" :key="discapacidad._id" :value="discapacidad.tipo">
              {{ discapacidad.tipo }}
            </option>
          </select>
        </div>
      </div>


        <div class="col-md-4">
          <div class="form-group">
            <label for="fechanacimiento">Fecha Nacimiento:</label>
            <input type="date" id="fechanacimiento" v-model="formData.fechanacimiento" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="telfdomicilio">Teléfono Domicilio:</label>
            <input type="text" id="telfdomicilio" v-model="formData.telfdomicilio" class="form-control" />
          </div>
        </div>
      </div>

      <!-- Fila 3 -->
      <div class="row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="celular">Celular:</label>
            <input type="text" id="celular" v-model="formData.celular" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="direccion">Dirección:</label>
            <input type="text" id="direccion" v-model="formData.direccion" style="text-transform:uppercase" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
  <div class="form-group">
    <label for="nombrePadres">Nombre de los Padres:</label>
    <textarea id="nombrePadres" v-model="formData.nombrePadres" class="form-control" style="text-transform:uppercase" rows="2"></textarea>
  </div>
</div>

      </div>

      <!-- Fila 4 -->
      <div class="row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="zona">Zona:</label>
            <input type="text" id="zona" v-model="formData.zona" style="text-transform:uppercase" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="provincia">Provincia:</label>
            <input type="text" id="provincia" v-model="formData.provincia" style="text-transform:uppercase" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="canton">Cantón:</label>
            <input type="text" id="canton" v-model="formData.canton" style="text-transform:uppercase" class="form-control" />
          </div>
        </div>
      </div>

      <!-- Fila 5 -->
      <div class="row">
        <div class="col-md-4">
          <div class="form-group">
            <label for="parroquia">Parroquia:</label>
            <input type="text" id="parroquia" v-model="formData.parroquia" style="text-transform:uppercase" class="form-control" />
          </div>
        </div>
        <div class="col-md-4">
        <div class="form-group">
          <label for="establecimiento">Establecimiento:</label>
          <select id="establecimiento" v-model="formData.establecimiento" class="form-control">
            <option v-for="establecimiento in establecimientos" :key="establecimiento._id" :value="establecimiento.nombre">
              {{ establecimiento.nombre }}
            </option>
          </select>
        </div>
      </div>
        <div class="col-md-4">
          <div class="form-group">
            <label for="sexo">Sexo:</label>
            <select id="sexo" v-model="formData.sexo" class="form-control">
              <option value="masculino">Masculino</option>
              <option value="femenino">Femenino</option>
            </select>
          </div>
        </div>
      </div>

      <!-- Fila 6 -->
      <div class="row">
        <div class="col-md-12">
          <button type="submit" class="btn btn-primary">Guardar</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import swal from 'sweetalert';
import { BASE_URL } from '../store/config';

export default {
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  data() {
    return {
      message: '',
      formData: {
        hcu: '',
        cedula: '',
        cedulamadre: '',
        nombres: '',
        apellidos: '',
        discapacidad: '',
        detalleDiscapacidad:'',
        fechanacimiento: null,
        telfdomicilio: '',
        celular: '',
        direccion: '',
        nombrePadres: '',
        zona: '',
        provincia: '',
        canton: '',
        parroquia: '',
        establecimiento: '',
        sexo: '',
        createdBy: '',
      },
      establecimientos: [], 
      discapacidades: []
    };
  },
  methods: {
    submitForm() {
      this.formData.createdBy = this.currentUser.id;
      //Mayúscula
      this.formData.nombres = this.formData.nombres.toUpperCase();
      this.formData.apellidos = this.formData.apellidos.toUpperCase();
      this.formData.direccion = this.formData.direccion.toUpperCase();
      this.formData.nombrePadres = this.formData.nombrePadres.toUpperCase();
      this.formData.zona = this.formData.zona.toUpperCase();
      this.formData.provincia = this.formData.provincia.toUpperCase();
      this.formData.canton = this.formData.canton.toUpperCase();
      this.formData.parroquia = this.formData.parroquia.toUpperCase();

      axios
        .post(`${BASE_URL}/api/nino`, this.formData, {
          headers: {
            'x-access-token': this.$store.state.auth.user.accessToken,
          },
        })
        .then(() => {
          swal({
            title: '¡Información Guardada!',
            text: 'La información del niño ha sido guardada correctamente.',
            icon: 'success',
            button: 'OK',
          });

          this.$router.push('/lista/ninos');
        })
        .catch((error) => {
          // Manejo de errores
          swal({
            title: 'Error',
            text: 'Hubo un error al guardar la información.',
            icon: 'error',
            button: 'OK',
          });
          this.message =
            (error.response && error.response.data && error.response.data.message) ||
            error.message ||
            error.toString();
        });
    },
    //listado establecimiento
    async fetchEstablecimientos() {
      try {
        const response = await axios.get(`${BASE_URL}/api/establecimiento`, {
          headers: {
            'x-access-token': this.$store.state.auth.user.accessToken,
          },
        });
        this.establecimientos = response.data;
      } catch (error) {
        console.error('Error al obtener establecimientos de salud:', error);
      }
    },
     //LISTADO DISCAPACIDAD 
  async fetchDiscapacidades() {
      try {
        const response = await axios.get(`${BASE_URL}/api/discapacidad`, {
          headers: {
            'x-access-token': this.$store.state.auth.user.accessToken,
          },
        });
        this.discapacidades = response.data;
      } catch (error) {
        console.error('Error al obtener discapacidades:', error);
      }
    },
  },
  mounted() {
    this.fetchEstablecimientos();
    this.fetchDiscapacidades(); 
  },
};
</script>

<style scoped>
/* Estilos adicionales si es necesario */
</style>
